'use client';

import React, { useState, useEffect, useRef, useMemo } from 'react';
import { Search, Navigation, Menu, X, ChevronRight, MapPin, Sparkles, Trophy, Star, Award, Dices, TrendingUp, Bike, Ship, Flag } from 'lucide-react';
import { LotteryStore, StoreType, LuckAnalysis } from '../../lib/types';
import { analyzeLuckLocal } from '../../lib/luckEngine'; // 가짜 AI 엔진 연동
import { MOCK_STORES } from '../../lib/data'; // 데이터 (기존 mockStores.ts 내용을 lib/data.ts로 이동 가정)

// Kakao Maps API 로딩 확인을 위한 타입 선언 (전역 kakao 객체)
declare global {
  interface Window {
    kakao: any;
  }
}

// 아이콘 설정 (기존 코드 유지)
const TYPE_CONFIG: Record<StoreType, { label: string, color: string, icon: React.ReactNode }> = {
  [StoreType.LOTTO]: { label: '로또 6/45', color: 'bg-yellow-500', icon: <Trophy className="w-4 h-4" /> },
  [StoreType.SPORTS_TOTO]: { label: '스포츠토토', color: 'bg-blue-500', icon: <Flag className="w-4 h-4" /> },
  [StoreType.PENSION]: { label: '연금복권', color: 'bg-purple-500', icon: <Star className="w-4 h-4" /> },
  [StoreType.HOTSPOT]: { label: '당첨명당', color: 'bg-red-500', icon: <Award className="w-4 h-4" /> },
  [StoreType.CASINO]: { label: '카지노', color: 'bg-slate-800', icon: <Dices className="w-4 h-4" /> },
  [StoreType.HORSE_RACING]: { label: '경마', color: 'bg-green-600', icon: <TrendingUp className="w-4 h-4" /> },
  [StoreType.BICYCLE_RACING]: { label: '경륜', color: 'bg-orange-500', icon: <Bike className="w-4 h-4" /> },
  [StoreType.BOAT_RACING]: { label: '경정', color: 'bg-cyan-500', icon: <Ship className="w-4 h-4" /> },
};

export default function MapInterface() {
  const [stores] = useState<LotteryStore[]>(MOCK_STORES);
  const [selectedStore, setSelectedStore] = useState<LotteryStore | null>(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [activeFilter, setActiveFilter] = useState<StoreType | 'ALL'>('ALL');
  const [isSidebarOpen, setIsSidebarOpen] = useState(true);
  const [analysis, setAnalysis] = useState<LuckAnalysis | null>(null);
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  
  const mapRef = useRef<any>(null); // Kakao Map 객체를 저장할 ref
  const markersRef = useRef<{ [key: string]: any }>({}); // Kakao Map Marker 객체를 저장할 ref

  // 모바일/데스크탑 감지
  useEffect(() => {
    setIsSidebarOpen(window.innerWidth > 768);
  }, []);

  const filteredStores = useMemo(() => {
    return stores.filter(s => {
      const matchSearch = s.name.includes(searchTerm) || s.address.includes(searchTerm);
      const matchType = activeFilter === 'ALL' || s.type.includes(activeFilter as StoreType);
      return matchSearch && matchType;
    });
  }, [stores, searchTerm, activeFilter]);

  // 지도 초기화
  useEffect(() => {
    if (typeof window === 'undefined' || !window.kakao || !window.kakao.maps) return;
    
    const mapContainer = document.getElementById('map-container');
    const mapOption = {
      center: new window.kakao.maps.LatLng(37.5665, 126.9780), // 서울 시청
      level: 8, // 지도의 확대 레벨
    };

    if (!mapRef.current) {
      mapRef.current = new window.kakao.maps.Map(mapContainer, mapOption);
    } else {
      // 기존 맵이 있다면 중심과 레벨만 재설정
      mapRef.current.setCenter(mapOption.center);
      mapRef.current.setLevel(mapOption.level);
    }

    // 기존 마커 제거
    Object.values(markersRef.current).forEach(m => m.setMap(null));
    markersRef.current = {};

    // 새로운 마커 추가
    filteredStores.forEach(store => {
      const markerPosition = new window.kakao.maps.LatLng(store.lat, store.lng);
      const marker = new window.kakao.maps.Marker({
        position: markerPosition,
        map: mapRef.current,
      });
      
      window.kakao.maps.event.addListener(marker, 'click', () => {
        setSelectedStore(store);
      });
      markersRef.current[store.id] = marker;
    });
  }, [filteredStores]);

  // 선택 시 이동
  useEffect(() => {
    if (selectedStore && mapRef.current && window.kakao && window.kakao.maps) {
      const moveLatLon = new window.kakao.maps.LatLng(selectedStore.lat, selectedStore.lng);
      mapRef.current.setCenter(moveLatLon);
      mapRef.current.setLevel(3); // 상세 보기 레벨
      setAnalysis(null);
    }
  }, [selectedStore]);

  // [핵심] 가짜 분석 실행 (딜레이를 줘서 진짜처럼 보이게 함)
  const handleAnalyze = async () => {
    if (!selectedStore) return;
    setIsAnalyzing(true);
    
    // 0.8초의 인위적인 지연 (사용자 기대감 고조)
    setTimeout(() => {
      const result = analyzeLuckLocal(selectedStore);
      setAnalysis(result);
      setIsAnalyzing(false);
    }, 800);
  };

  return (
    <div className="flex h-screen bg-slate-50 font-sans text-slate-900 relative overflow-hidden">
      {/* 사이드바 (정보창) */}
      <aside className={`absolute md:relative z-20 h-full bg-white border-r shadow-2xl transition-all duration-300 ease-in-out ${isSidebarOpen ? 'w-full md:w-96 translate-x-0' : '-translate-x-full md:w-0 md:translate-x-0 md:overflow-hidden'}`}>
        <div className="flex flex-col h-full">
            {/* 상단 헤더 & 검색 */}
            {!selectedStore ? (
               <div className="flex flex-col h-full">
                  <div className="p-6 space-y-4 border-b">
                    <div className="flex justify-between items-center">
                        <h1 className="text-xl font-black tracking-tight text-slate-900 flex items-center">
                            <MapPin className="mr-2 text-indigo-600" /> LottoMap
                        </h1>
                        <button onClick={() => setIsSidebarOpen(false)} className="md:hidden p-2"><X /></button>
                    </div>
                    <div className="relative">
                        <Search className="absolute left-3 top-3 text-slate-400 w-4 h-4" />
                        <input 
                            type="text" 
                            placeholder="지역, 가게명 검색..." 
                            className="w-full pl-10 pr-4 py-2 bg-slate-100 rounded-xl text-sm focus:outline-none focus:ring-2 focus:ring-indigo-500"
                            value={searchTerm}
                            onChange={(e) => setSearchTerm(e.target.value)}
                        />
                    </div>
                    {/* 필터 칩 영역 (생략 가능, 공간상) */}
                  </div>
                  
                  {/* 리스트 영역 */}
                  <div className="flex-1 overflow-y-auto p-4 space-y-2">
                     {filteredStores.map(s => (
                        <div key={s.id} onClick={() => setSelectedStore(s)} className="p-4 rounded-2xl hover:bg-slate-50 border border-transparent hover:border-indigo-100 cursor-pointer transition-all flex items-center space-x-3 group">
                            <div className={`p-2 rounded-lg ${TYPE_CONFIG[s.type[0]]?.color} text-white`}>
                                {TYPE_CONFIG[s.type[0]]?.icon}
                            </div>
                            <div className="flex-1">
                                <h3 className="font-bold text-sm group-hover:text-indigo-600">{s.name}</h3>
                                <p className="text-xs text-slate-500 truncate">{s.address}</p>
                            </div>
                            <ChevronRight className="w-4 h-4 text-slate-300" />
                        </div>
                     ))}
                  </div>
               </div>
            ) : (
                // 상세 정보 뷰
                <div className="flex flex-col h-full animate-in slide-in-from-right">
                    <div className="p-4 border-b flex items-center">
                        <button onClick={() => setSelectedStore(null)} className="flex items-center text-sm font-bold text-slate-600 hover:text-indigo-600">
                            <ChevronRight className="w-4 h-4 rotate-180 mr-1" /> 목록으로
                        </button>
                    </div>
                    
                    <div className="p-6 overflow-y-auto flex-1 space-y-6">
                        <div>
                            <span className="inline-block px-2 py-1 bg-indigo-100 text-indigo-700 text-[10px] font-black rounded-md mb-2">HOT PLACE</span>
                            <h2 className="text-2xl font-black text-slate-900">{selectedStore.name}</h2>
                            <p className="text-sm text-slate-500 mt-1">{selectedStore.address}</p>
                        </div>

                        {/* 분석 버튼 영역 */}
                        <div className="bg-slate-900 rounded-3xl p-6 text-white shadow-xl relative overflow-hidden">
                            <div className="absolute top-0 right-0 w-32 h-32 bg-indigo-500 rounded-full blur-3xl opacity-20 -mr-10 -mt-10"></div>
                            
                            {!analysis ? (
                                <div className="text-center py-4">
                                    <Sparkles className="w-8 h-8 mx-auto mb-3 text-yellow-400 animate-pulse" />
                                    <h3 className="font-bold text-lg mb-1">이곳의 기운이 궁금한가요?</h3>
                                    <p className="text-slate-400 text-xs mb-4">AI가 풍수지리와 당첨 데이터를 분석합니다.</p>
                                    <button 
                                        onClick={handleAnalyze}
                                        disabled={isAnalyzing}
                                        className="w-full bg-white text-slate-900 font-black py-3 rounded-xl hover:scale-105 transition-transform active:scale-95 disabled:opacity-70"
                                    >
                                        {isAnalyzing ? "기운 분석중..." : "무료 분석 시작하기"}
                                    </button>
                                </div>
                            ) : (
                                <div className="animate-in zoom-in space-y-4">
                                    <div className="flex justify-between items-end border-b border-white/10 pb-4">
                                        <div>
                                            <p className="text-indigo-300 text-xs font-bold uppercase">Luck Score</p>
                                            <p className="text-4xl font-black text-yellow-400">{analysis.score}<span className="text-base text-white">/100</span></p>
                                        </div>
                                        <div className="text-right">
                                            <p className="text-xs text-slate-400">AI 추천</p>
                                        </div>
                                    </div>
                                    <p className="text-sm font-medium leading-relaxed opacity-90">"{analysis.insights}"</p>
                                    
                                    <div className="bg-white/10 rounded-xl p-3 backdrop-blur-sm">
                                        <p className="text-xs text-indigo-200 font-bold mb-2 uppercase">Lucky Numbers</p>
                                        <div className="flex justify-between gap-1">
                                            {analysis.luckyNumber.map(n => (
                                                <span key={n} className="w-8 h-8 flex items-center justify-center bg-white text-slate-900 rounded-full font-black text-xs shadow-lg">{n}</span>
                                            ))}
                                        </div>
                                    </div>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            )}
        </div>
      </aside>

      {/* 지도 영역 */}
      <main className="flex-1 relative h-full w-full">
         <div id="map-container" className="h-full w-full z-0 bg-slate-200"></div>
         
         {/* 토글 버튼 (모바일용은 별도 처리 필요) */}
         <button 
            onClick={() => setIsSidebarOpen(!isSidebarOpen)}
            className="absolute top-4 left-4 z-10 bg-white p-3 rounded-full shadow-lg hover:bg-slate-50 hidden md:block"
         >
            {isSidebarOpen ? <X className="w-5 h-5" /> : <Menu className="w-5 h-5" />}
         </button>
      </main>
    </div>
  );
}